@import url(https://fonts.googleapis.com/css2?family=Kumbh+Sans:wght@100;200;300;400;500;600;700;800;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{padding:0;margin:0;box-sizing:border-box}html{font-family:'Kumbh Sans', sans-serif;font-size:62.5%}body{max-width:1440px;margin:0 auto;background-color:#f3f5f7}body.dark-theme{background-color:#121721}h1{font-size:2.8rem}h2{font-size:2.4rem}h3{font-size:2rem}h4{font-size:1.4rem}::-webkit-input-placeholder{font-size:1.6rem}:-ms-input-placeholder{font-size:1.6rem}p,li,input,a,::placeholder,button,label,span{font-size:1.6rem}li{list-style-type:none}a{text-decoration:none}

.header{background:url(/devjobs-web-app/static/media/bg-pattern-header.f3307602.svg);padding:4rem 0rem;height:16rem}@media (max-width: 768px){.header{background:url(/devjobs-web-app/static/media/bg-pattern-header.79b7b4cc.svg)}}@media (max-width: 375px){.header{background:url(/devjobs-web-app/static/media/bg-pattern-header.ea1134f1.svg);height:13.6rem}}.header .header-wrapper{display:flex;align-items:center;width:85%;margin:0 auto;justify-content:space-between}@media (max-width: 781px){.header .header-wrapper{width:90%}}.header .header-wrapper .title{color:#fff;cursor:pointer}.header .header-wrapper .moon-icon{margin-top:0.6rem}.header .header-wrapper .sun-icon{margin-top:0.3rem}.header .header-wrapper .theme-toggle{display:flex}.header .header-wrapper .theme-toggle-button{height:1.4rem;width:1.4rem;margin:0 0.4rem;border-radius:99px;background-color:#5762e0;transition:all 0.4s ease}.header .header-wrapper .theme-toggle-button.dark{margin-left:2.9rem}.header .header-wrapper .theme-toggle-button-wrapper{cursor:pointer;display:flex;align-items:center;height:2.4rem;width:4.8rem;border-radius:99px;background-color:#fff;margin:0 1rem}

.search-component{display:flex;background-color:#fff;width:85%;margin:0 auto;margin-top:5rem;border-radius:5px;justify-content:space-evenly}@media (max-width: 781px){.search-component{width:90%}}.search-component input{border:none}.search-component .filter-location{border-left:1px solid #9eafc2;border-right:1px solid #9eafc2}.search-component .filter-location input{max-width:14rem;text-overflow:ellipsis;width:100%}.search-component .filter-icon{padding:0rem 1rem}.search-component .filter-location,.search-component .filter-title,.search-component .right-wrapper{padding:1rem;display:flex;align-items:center;width:100%}.search-component .filter-location form,.search-component .filter-title form,.search-component .right-wrapper form{display:flex}@media (max-width: 768px){.search-component .filter-location,.search-component .filter-title,.search-component .right-wrapper{padding:0.5rem}}.search-component .filter-title{max-width:40rem}.search-component .filter-title input{text-overflow:ellipsis;width:100%}.search-component .filter-title form{width:100%}@media (max-width: 500px){.search-component .filter-title{padding-left:2rem}}.search-component .right-wrapper{justify-content:space-between}@media (max-width: 500px){.search-component .right-wrapper{justify-content:flex-end;flex:1 1}.search-component .right-wrapper .modal-btn{margin:0 1.5rem;cursor:pointer}}.search-component .right-wrapper .checkbox{display:flex;align-items:center}.search-component .right-wrapper .label{font-weight:600;margin:0 0.5rem}body.dark-theme .search-component{background-color:#19212e}body.dark-theme .search-component input{background-color:#19212e;color:#fff}body.dark-theme .search-component .label{color:#fff}body.dark-theme .search-component .modal-btn img{-webkit-filter:brightness(900%);filter:brightness(900%)}body.dark-theme .search-component .filter-location{background-color:#19212e}body.dark-theme .search-component .bottom-wrapper{background-color:#19212e}

.btn{border-radius:5px;padding:1rem 2rem;font-family:inherit;font-weight:500;border:none;cursor:pointer;transition:all 0.25s ease;display:flex;align-items:center}.btn.mobile{padding:1rem}.btn svg{height:2.4rem;width:3rem}.btn.one{color:#fff;background-color:#5762e0}.btn.one:hover{background-color:#949cf4}.btn.two{color:#5762e0;background-color:#eeeffc}.btn.two:hover{background-color:#c3c7f4}.btn.two.dark{color:#fff;background-color:#2f3541}.btn.two.dark:hover{background-color:#696f77}

.checkbox-input{display:flex;align-items:center;min-width:2.4rem;min-height:2.4rem;background-color:#e7e7e9;cursor:pointer;border-radius:5px;transition:all 0.3s ease;position:relative}.checkbox-input img{position:absolute;left:50%;top:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}.checkbox-input.checked{background-color:#5762e0}.checkbox-input:hover{background-color:#5762e0}body.dark-theme .checkbox-input{background-color:#2f3541}body.dark-theme .checkbox-input.checked{background-color:#5762e0}body.dark-theme .checkbox-input:hover{background-color:#5762e0}

.overlay-wrapper{position:absolute;top:0;bottom:0;right:0;left:0;background-color:rgba(0,0,0,0.5)}.modal-wrapper{position:absolute;display:flex;align-items:center;justify-content:center;top:0;bottom:0;right:0;left:0;z-index:9}.modal-wrapper .modal{background-color:#fff;border-radius:5px;width:85%;z-index:10}.modal-wrapper .modal .filter-location{padding:2rem;border:none;border-bottom:1px solid #9eafc2}.modal-wrapper .modal .bottom-wrapper{padding:2rem}.modal-wrapper .modal .bottom-wrapper .label{font-weight:500;margin-left:1rem}.modal-wrapper .modal .bottom-wrapper .checkbox{display:flex;margin:1rem 0}.modal-wrapper .modal .bottom-wrapper .search-btn button{width:100%;display:flex;justify-content:center}body.dark-theme .modal{background-color:#19212e}

.job-component{min-width:32rem;position:relative;border-radius:5px;margin-top:5rem;padding:3rem;cursor:pointer;background-color:#fff}.job-component:hover{color:#6d7f97}@media (max-width: 375px){.job-component{min-width:28rem}}.job-component .logo{position:absolute;width:5rem;height:5rem;display:flex;align-items:center;justify-content:center;border-radius:15px;top:0;-webkit-transform:translateY(-50%);transform:translateY(-50%)}.job-component .details{display:flex;flex-direction:column;margin-top:1rem}.job-component .details .location{margin-top:4rem;color:#5762e0;font-weight:600}.job-component .details .position{margin:1rem 0}.job-component .details .company,.job-component .details .time{color:#6d7f97}body.dark-theme .job-component{background-color:#19212e;color:#fff}body.dark-theme .job-component:hover{color:#6d7f97}

.homepage{width:85%;margin:5rem auto}.homepage .jobs{display:grid;grid-gap:1rem;gap:1rem;grid-template-columns:repeat(auto-fit, minmax(32rem, 1fr));align-items:center}@media (max-width: 781px){.homepage .jobs{width:90%;justify-items:center}}@media (max-width: 375px){.homepage .jobs{grid-template-columns:repeat(auto-fit, minmax(28rem, 1fr))}}.homepage .loadBtn{display:flex;justify-content:center;margin-top:2rem}

.listing-page{max-width:73rem;margin:0 auto;padding:0 1rem}.listing-page .listing{margin-top:2rem;background-color:#fff;border-radius:5px;padding:5rem}@media (max-width: 480px){.listing-page .listing{padding:4rem 2.5rem}}.listing-page .listing .top-shelf{display:flex;justify-content:space-between;align-items:center}.listing-page .listing .top-shelf .time{color:#6d7f97}.listing-page .listing .top-shelf .position{margin:1rem 0}.listing-page .listing .top-shelf .location{font-weight:500;color:#5762e0}@media (max-width: 480px){.listing-page .listing .top-shelf{flex-direction:column;align-items:baseline}.listing-page .listing .top-shelf .button{margin-top:5rem;width:100%}.listing-page .listing .top-shelf .button button{width:100%;display:flex;justify-content:center}}.listing-page .listing .description{margin:4rem 0;color:#6d7f97}.listing-page .listing .requirements h3{margin-bottom:2rem}.listing-page .listing .requirements p{color:#6d7f97}.listing-page .listing .requirements .requirement{color:#6d7f97;list-style:disc;margin:2rem 0rem 2rem 2rem}.listing-page .listing .requirements .requirement::marker{color:#5762e0}.listing-page .listing .requirements .requirement span{position:relative;left:1rem}.listing-page .listing .role h3{margin-bottom:2rem}.listing-page .listing .role p,.listing-page .listing .role span{color:#6d7f97}.listing-page .listing .role .role-li{list-style-type:inherit;margin:2rem 0rem 2rem 1.7rem}.listing-page .listing .role .role-li::marker{font-weight:500;color:#5762e0}.listing-page .listing .role .role-li span{position:relative;left:1rem}body.dark-theme .listing{background-color:#19212e}body.dark-theme .listing .position,body.dark-theme .listing h3{color:#fff}

footer{background-color:#fff;margin-top:5rem;padding:2.5rem 4rem;display:flex;justify-content:space-between;align-items:center;border-radius:5px}footer .info p{margin-top:1rem;color:#6d7f97}footer .info h2{margin-right:1rem}@media (max-width: 480px){footer .info{display:none}}@media (max-width: 480px){footer{padding:2.5rem}footer .button{width:100%}footer .button button{width:100%;display:flex;justify-content:center}}body.dark-theme footer{background-color:#19212e}body.dark-theme footer h2{color:#fff}

.listing-header{max-width:73rem;height:14rem;border-radius:5px;background-color:#fff;margin:0 auto;margin-top:-4rem;display:flex;overflow:hidden}@media (max-width: 480px){.listing-header{position:relative;overflow:visible;height:auto}}.listing-header .logo{width:14rem;display:flex;align-items:center;justify-content:center}.listing-header .logo img{width:8rem}@media (max-width: 480px){.listing-header .logo{position:absolute;width:5rem;height:5rem;display:flex;align-items:center;justify-content:center;border-radius:15px;top:0;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}.listing-header .logo img{width:4rem}}.listing-header .header-main{display:flex;justify-content:space-between;align-items:center;flex:1 1;padding:4rem}@media (max-width: 480px){.listing-header .header-main{flex-direction:column}.listing-header .header-main .company-name{text-align:center}.listing-header .header-main .company-name h2{font-size:2rem}.listing-header .header-main .button{margin-top:3rem}}.listing-header .header-main .company-name h2{margin-bottom:2rem}.listing-header .header-main .company-name p{color:#6d7f97}body.dark-theme .listing-header{background-color:#19212e}body.dark-theme .listing-header h2{color:#fff}

